const BASE_URL = 'http://124.223.107.207:3001'
export const myRequest = (options) => {
	return new Promise((resolve,reject) => {
		uni.request({
			url: BASE_URL + options.url,
			method: options.method || 'GET',
			data: options.data || {},
			success: (res)=>{
				if(res.statusCode !== 200){
					return uni.showToast({
						title:""
					})
				}
				resolve(res)
			},
			fail:(err)=>{
				uni.showToast({
					title:''
				})
				reject(err)
			}
		})
	})

}